.App{
  display: flex;
  align-items: center;
  justify-items: center;
  align-content: center;
  background: #140F2D;
  justify-content: center;
  height: 100vh;
}
.item{
  height: 80px;
  width: 120px;
  border: 2px solid #140F2D;
  color: #140F2D;
  background-color: #3F88C5;
  padding: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}
.bingoWrapper{
  display: flex;
  flex-direction: column;
}
.row{
  display: flex;
  flex-direction: row;
}
.selected{
  background-color: #F49D37;
}